import React from 'react';
import { connect } from 'react-redux';

import Header from './../header';
import Nav from './../nav';
import ToTop from './../toTop';
import Banner from './../banner';
import NewsList from './../newsList';
import NewsNext from './../newsNext';

import s from './News';


function News(data) {
    return (
        <div className={s.box}>
            <Header data={data.data.forecast} />
            <Nav />
            <Banner />
            <NewsList data={data.data.news.items} />
            <NewsNext page={data.data.news.page_no} />
            <ToTop />
        </div>
    );
}

export default connect(
    state => ({
        data: state,
    })
)(News);




